import { useNavigate } from "react-router-dom";
import { useState, useEffect, useRef } from "react";
import axios from "axios";
import { toast, ToastContainer } from "react-toastify";

const PackagePageLayer = () => {
  const navigate = useNavigate();
  const [packages, setPackages] = useState([]);
  // const [filteredPackages, setFilteredPackages] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [selectedPackage, setSelectedPackage] = useState(null);
  // const [searchQuery, setSearchQuery] = useState("");
  const [name,setName] = useState("");
  
  // Create a ref for the modal content
  const modalRef = useRef(null);

  // Fetch all packages
  // useEffect(() => {
  //   const fetchPackages = async () => {
  //     try {
  //       const centreId = localStorage.getItem("centreId");
  //       const response = await axios.get(
  //         `${process.env.REACT_APP_BACKEND_URL}/api/packages/${centreId}`
  //       );
  //       setPackages(response.data.packages || []);
  //       setFilteredPackages(response.data.packages || []);
  //     } catch (error) {
  //       console.error("Failed to fetch packages:", error);
  //     }
  //   };

  //   fetchPackages();
  // }, []);

     const fetchSearchedPackages = async (query) => {
    try {
      const centreId = localStorage.getItem("centreId");
      if (!centreId) return;

      const res = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/packages/searchPackage?query=${query}&centreId=${centreId}`
      );
      setPackages(res.data.data);
    } catch (error) {
      console.error("Error fetching users:", error);
      setPackages([]);
    }
  };

  const fetchPackages = async () => {
    const centreId = localStorage.getItem("centreId");
    if (!centreId) return;

    try {
      const response = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/packages/${centreId}`
      );
      setPackages(response.data.packages || []);
    } catch (error) {
      console.error("Error fetching doctors:", error);
    }
  };


  

  useEffect(() => {
    const delayDebounce = setTimeout(() => {
      if (name.trim() !== "") {
        fetchSearchedPackages(name);
      } else {
        fetchPackages();
      }
    }, 500);

    return () => clearTimeout(delayDebounce);
  }, [name]);

  useEffect(() => {
    fetchPackages();
  }, []);

 


  // Add useEffect to handle outside clicks
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (modalRef.current && !modalRef.current.contains(event.target)) {
        setShowModal(false);
      }
    };

    if (showModal) {
      document.addEventListener("mousedown", handleClickOutside);
    }

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [showModal]);
    
  useEffect(() => {
    // Handle click outside
    const handleClickOutside = (event) => {
      if (
        modalRef.current &&
        !modalRef.current.contains(event.target) &&
        showModal
      ) {
        setShowModal(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);

    // Scroll lock logic
    if (showModal) {
      document.body.style.overflow = "hidden";
    } else {
      document.body.style.overflow = "auto";
    }

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
      document.body.style.overflow = "auto";
    };
  }, [showModal]);

 

  const handleViewDetails = (pkg) => {
    setSelectedPackage(pkg);
    setShowModal(true);
  };

  const handleEdit = (id) => {
    navigate(`/edit-package/${id}`);
  }

  // Pagination logic - Use filteredPackages instead of packages
  const [currentPage, setCurrentPage] = useState(1);
  const packagesPerPage = 10;
  const indexOfLastPackage = currentPage * packagesPerPage;
  const indexOfFirstPackage = indexOfLastPackage - packagesPerPage;
  const currentPackages = packages.slice(indexOfFirstPackage, indexOfLastPackage);
  const totalPages = Math.ceil(packages.length / packagesPerPage);

  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

      const handlePageChangeWithScroll = (page) => {
  handlePageChange(page); // your existing page change logic
  window.scrollTo({ top: 0, behavior: "smooth" }); // scroll to top smoothly
};


  const renderPagination = () => {
    const pages = [];
    const maxVisible = 4;
    let startPage = Math.max(1, currentPage - Math.floor(maxVisible / 2));
    let endPage = startPage + maxVisible - 1;

    if (endPage > totalPages) {
      endPage = totalPages;
      startPage = Math.max(1, endPage - maxVisible + 1);
    }

         pages.push(
    <li
      key="first"
      className={`page-item ${currentPage === 1 ? " disabled" : ""}`}
    >
      <button
        className="page-link"
        onClick={() => currentPage !== 1 && handlePageChangeWithScroll(1)}
       
      >
        First
      </button>
    </li>
  );

    // Prev Button
    pages.push(
      <li key="prev" className={`page-item ${currentPage === 1 ? "disabled" : ""}`}>
        <button
          className="page-link"
          onClick={() => currentPage > 1 && handlePageChangeWithScroll(currentPage - 1)}
        >
          Prev
        </button>
      </li>
    );

    // Page Numbers
    for (let i = startPage; i <= endPage; i++) {
      pages.push(
        <li key={i} className={`page-item ${currentPage === i ? "active" : ""}`}>
          <button className="page-link" onClick={() =>handlePageChangeWithScroll(i)}>
            {i}
          </button>
        </li>
      );
    }

    // Next Button
    pages.push(
      <li
        key="next"
        className={`page-item ${currentPage === totalPages ? "disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() => currentPage < totalPages && handlePageChangeWithScroll(currentPage + 1)}
        >
          Next
        </button>
      </li>
    );

         pages.push(
      <li
        key="last"
        className={`page-item ${currentPage === totalPages ? "disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() => handlePageChangeWithScroll(totalPages)}
        >
          Last
        </button>
      </li>
    );


    return pages;
  };

  return (
    <div className="dashboard-body__content">
      {/* Packages Table */}
      <div className="card common-card border border-gray-five">
        <div className="card-body">
          {/* Header Section */}
          <div className="package-header">
            <h4 className="package-title">Available Packages</h4>
            <div className="package-actions">
              <input
                type="text"
                className="common-input border-gray-five package-search-input"
                value={name}
                onChange={(e) => setName(e.target.value)}
                placeholder="Search by package name"
              />
              <button
                className="add-package-btn"
                onClick={() => navigate("/create-package")}
              >
                Create Packages
              </button>
            </div>
          </div>

          {currentPackages.length > 0 ? (
            <div className="table-responsive">
              <table className="table table-sm d text-body align-middle">
                <thead className="table-light">
                  <tr>
                    <th>SN.</th>
                    <th>Package Name</th>
                    <th>Included Services</th>
                    <th>Actual Price</th>
                    <th>Platform Fees</th>
                    <th>Doctors</th>
                    <th>Time Slots</th>
                    <th>Description</th>
                    <th>Action</th>
                  </tr>
                </thead>
                <tbody>
                  {currentPackages.map((pkg, index) => (
                    <tr key={pkg._id}>
                      <td>{indexOfFirstPackage + index + 1}</td>
                      <td style={{ textAlign: "left" }}>{pkg.packageName}</td>
                      <td style={{ textAlign: "left" }}>
                        <ul className="mb-0 p-0 list-unstyled">
                          {pkg.services.slice(0, 3).map((srv, idx) => (
                            <li key={idx}>â¢ {srv.serviceName}</li>
                          ))}
                        </ul>

                        {pkg.services.length > 3 && (
                          <button
                            style={{
                              background: "none",
                              border: "none",
                              color: "blue",
                              padding: 0,
                              marginTop: "5px",
                              cursor: "pointer",
                              textDecoration: "underline",
                              fontSize: "14px",
                            }}
                            onClick={() => {
                              setSelectedPackage(pkg);
                              setShowModal(true);
                            }}
                          >
                            View More
                          </button>
                        )}
                      </td>
                      <td>
                        â¹
                        {pkg?.discountPrice
                          ? pkg.discountPrice
                          : pkg.actualPrice}
                      </td>

                      <td>â¹{pkg.platformFees}</td>
                      <td style={{ textAlign: "left" }}>
                        {pkg?.doctors?.slice(0, 3).map((doc, i) => (
                          <div key={i}>
                            {doc.name}
                            {i === 2 && pkg.doctors.length > 3 && (
                              <span
                                style={{
                                  fontSize: "12px",
                                  color: "blue",
                                  cursor: "pointer",
                                  marginLeft: "8px",
                                  textDecoration: "underline",
                                }}
                                onClick={() => handleViewDetails(pkg)}
                              >
                                View More
                              </span>
                            )}
                          </div>
                        ))}
                      </td>

                      <td style={{ textAlign: "left" }}>
                        {pkg.slots.slice(0, 3).map((slot, i) => (
                          <div key={i}>
                            {slot.day.slice(0, 3)}: {slot.from} - {slot.to}
                          </div>
                        ))}
                        {pkg.slots.length > 3 && (
                          <span
                            style={{
                              fontSize: "12px",
                              color: "blue",
                              cursor: "pointer",
                              marginLeft: "8px",
                              textDecoration: "underline",
                            }}
                            onClick={() => handleViewDetails(pkg)}
                          >
                            View More
                          </span>
                        )}
                      </td>

                      <td
                        style={{
                          maxWidth: "300px",
                          overflow: "hidden",
                          whiteSpace: "nowrap",
                          textOverflow: "ellipsis",
                        }}
                      >
                        {pkg.description}
                      </td>
                      <td>
                        <button
                          onClick={() => {
                            setSelectedPackage(pkg);
                            setShowModal(true);
                          }}
                          style={{
                            backgroundColor: "#007bff",
                            color: "white",
                            border: "none",
                            borderRadius: "4px",
                            padding: "5px 10px",
                            marginRight: "5px",
                          }}
                        >
                          View
                        </button>
                        <button
                          type="button"
                          style={{
                            backgroundColor: "green",
                            color: "white",
                            border: "none",
                            borderRadius: "4px",
                            padding: "5px 10px",
                            marginRight: "5px",
                          }}
                          onClick={() => {
                            handleEdit(pkg?._id)
                          }}
                        >
                          Edit
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          ) : (
          <p className="text-muted">No services added yet.</p>
          )}

          {totalPages>1 && (
            <div className="flx-between gap-2">
              <div className="paginate-content flx-align flex-nowrap gap-3">
                <span className="paginate-content__text fs-14">
                  Showing {indexOfFirstPackage + 1} â{" "}
                   {Math.min(indexOfLastPackage, packages.length)} of{" "}
                    {packages.length}
                </span>
              </div>
              <div>
                <nav aria-label="Package pagination" className="mt-3">
                  <ul className="pagination justify-content-center">
                    {renderPagination()}
                  </ul>
                </nav>
              </div>
            </div>
          )}
        </div>
      </div>
      
      {/* Modal */}
      {showModal && selectedPackage && (
        <div
          style={{
            position: "fixed",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: "rgba(0,0,0,0.5)",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            zIndex: 1000,
          }}
          onClick={() => setShowModal(false)}
        >
          <div
            ref={modalRef}
            style={{
              backgroundColor: "#fff",
              borderRadius: "10px",
              maxWidth: "700px",
              width: "90%",
              boxShadow: "0 0 10px rgba(0,0,0,0.3)",
              position: "relative",
              maxHeight: "90vh",
              display: "flex",
              flexDirection: "column",
              overflow: "hidden",
            }}
            onClick={(e) => e.stopPropagation()}
          >
            <div
              style={{
                backgroundColor: "#3a3a3a",
                color: "#fff",
                padding: "15px 20px",
                fontSize: "18px",
                fontWeight: "bold",
                position: "sticky",
                top: 0,
                zIndex: 10,
              }}
            >
              Package Details
              <button
                onClick={() => setShowModal(false)}
                style={{
                  position: "absolute",
                  top: "10px",
                  right: "10px",
                  background: "transparent",
                  border: "none",
                  fontSize: "22px",
                  color: "#fff",
                  cursor: "pointer",
                }}
              >
                &times;
              </button>
            </div>

            <div
              style={{
                padding: "20px",
                overflowY: "auto",
                flex: 1,
              }}
            >
              <div
                style={{
                  display: "grid",
                  gridTemplateColumns: "1fr 1fr",
                  gap: "20px",
                }}
              >
                <div>
                  <div style={{ marginBottom: "20px" }}>
                    <p>
                      <strong style={{ color: "black" }}>Package Name</strong>
                    </p>
                    <p>{selectedPackage.packageName}</p>
                  </div>
                  <div style={{ marginBottom: "20px" }}>
                    <p>
                      <strong style={{ color: "black" }}>Actual Price</strong>
                    </p>
                    <p>â¹{selectedPackage.totalPrice}</p>
                  </div>
                  <div style={{ marginBottom: "20px" }}>
                    <p>
                      <strong style={{ color: "black" }}>Doctors</strong>
                    </p>
                    <ul
                      style={{
                        maxHeight: "150px",
                        overflowY: "auto",
                      }}
                    >
                      {selectedPackage.doctors.map((srv, idx) => (
                        <li key={idx}>{srv.name}</li>
                      ))}
                    </ul>
                  </div>
                </div>

                <div>
                  <div style={{ marginBottom: "20px" }}>
                    <p>
                      <strong style={{ color: "black" }}>Included Services</strong>
                    </p>
                    <ul
                      style={{
                        maxHeight: "150px",
                        overflowY: "auto",
                      }}
                    >
                      {selectedPackage.services.map((srv, idx) => (
                        <li key={idx}>{srv.serviceName}</li>
                      ))}
                    </ul>
                  </div>
                  <div style={{ marginBottom: "20px" }}>
                    <p>
                      <strong style={{ color: "black" }}>Platform Fee</strong>
                    </p>
                    <p>â¹{selectedPackage.platformFees}</p>
                  </div>
                  <div style={{ marginBottom: "20px" }}>
                    <p>
                      <strong style={{ color: "black" }}>Time Slots</strong>
                    </p>
                    <ul>
                      {selectedPackage.slots.map((slot, i) => (
                        <li key={i}>
                          {slot.day}: {slot.from} - {slot.to}
                        </li>
                      ))}
                    </ul>
                  </div>
                </div>
              </div>

              <div style={{ marginTop: "20px" }}>
                <p>
                  <strong style={{ color: "black" }}>Description</strong>
                </p>
                <p>{selectedPackage.description}</p>
              </div>
            </div>
          </div>
        </div>
      )}

      <ToastContainer position="top-right" autoClose={3000} />

      {/* Add this style tag at the end */}
      <style>
        {`
          .package-header {
            display: flex;
            justify-content: space-between;
            align-items: center;
            flex-wrap: wrap;
          }

          .package-actions {
            display: flex;
            gap: 15px;
            align-items: center;
          }

          .package-search-input {
            max-width: 250px;
            width: 100%;
          }

          .add-package-btn {
            background-color: green;
            color: whitesmoke;
            padding: 10px 16px;
            border-radius: 5px;
            font-weight: 600;
            border: none;
            cursor: pointer;
            white-space: nowrap;
          }

          /* Mobile Responsive for screens less than 521px */
          @media (max-width: 521px) {
            .package-header {
              flex-direction: column;
              align-items: flex-start;
              
            }

            .package-title {
              text-align: center;
              margin-bottom: 0;
            }

            .package-actions {
              flex-direction: column;
              gap: 10px;
              width: 100%;
              margin-top:7px;
            }

            .package-search-input {
              max-width: 100% !important;
              width: 100%;
            }

            .add-package-btn {
              width: 100%;
              padding: 12px;
            }
          }
        `}
      </style>
    </div>
  );
};

export default PackagePageLayer;